import React, { Component } from "react";
import LoadingBar from "./LoadingBar";
import MainOne from "./mains/MainOne";
import MainTwo from "./mains/MainTwo";
import MainThree from "./mains/MainThree";
import MainFour from "./mains/MainFour";
import MainFive from "./mains/MainFive";
import MainSix from "./mains/MainSix";
import Footer from "./Footer";

export class MainWindow extends Component {
  constructor() {
    super();

    /////////////////// DATA AND QUESTIONS STATES/////////////////////

    this.state = {
      isFetched: false,
      dataReceived: null,
      stageOne: true,
      stageTwo: false,
      stageThree: false,
      stageFour: false,
      stageFive: false,
      stageSix: false,
      // firstQ: true,
      // secQ: false,
      // thirdQ: false,
      // lastBlock: false,
      // firstDisplay: true,
      currentKey: 0
    };

    //////////////////// METHODS ARE BOUND TO STATES/////////////////////////////

    this.ChangeCurrentKey = this.ChangeCurrentKey.bind(this);
    this.FirstBtnClick = this.FirstBtnClick.bind(this);
    this.SecondBtnClick = this.SecondBtnClick.bind(this);
  }

  /////////////////////DATA FETCHING FROM DATABASE////////////////////////////

  componentWillMount() {
    let myData;
    fetch("http://95.217.34.225/randomlander/get/ML_PL_8sbIzG")
    // This route is dynamic and query after the "get" route is outdated
    // If you need to imitate a json response, please go to https://firstpushbucket.s3.eu-west-3.amazonaws.com/Amazon+Master+Lander/response.json" 
    // copy-paste-equate it to "var json"


      .then(res => res.json())
      .then(json => {
        myData = json;
        this.setState({
          isFetched: true,
          dataReceived: json
        });
      });



    
  }

  ///////////////LOCAL METHODS////////////////////////

  FirstBtnClick() {
    this.setState({
      stageOne: false,
      stageTwo: true
    });

    setTimeout(() => {
      this.setState({
        stageTwo: false,
        stageThree: true
      });
    }, 1800);
  }

  SecondBtnClick() {
    this.setState({
      stageThree: false,
      stageFour: true
    });
  }

  ChangeCurrentKey() {
    var { currentKey, dataReceived } = this.state;
    setTimeout(() => {
      this.setState(prevState => ({
        currentKey: prevState.currentKey + 1
      }));
    }, 250);

    if (currentKey + 1 === dataReceived.landerText.questions.length) {
      setTimeout(() => {
        this.setState({
          stageFour: false,
          stageFive: true
        });
      }, 500);

      setTimeout(() => {
        this.setState({
          stageSix: true
        });
        setTimeout(() => {
          this.setState({
            stageFive: false,
            stageSix: false,
            stageSeven: true
          });
          console.log("stageSeven: true");
        }, 3000);
        console.log("stageSix: true");
      }, 2000);
    }
  }

  render() {
    var {
      isFetched,
      dataReceived,
      stageOne,
      stageTwo,
      stageThree,
      stageFour,
      stageFive,
      stageSix,
      stageSeven,
      currentKey
    } = this.state;

    if (!isFetched) {
      return <LoadingBar />;
    } else
      return (
        <div>
          <style>
            {`
            :root {
              --buttonColor: ${dataReceived.brand.buttonColor};
              --hoverButtonColor: ${dataReceived.brand.hoverButtonColor};
              }
            `}
          </style>
          <div className="page">
            <header className="header">
              <img
                src={dataReceived.brand.logo}
                alt="Icon"
                className="header__icon"
              />
            </header>

            <MainOne
              stageOne={stageOne}
              stageTwo={stageTwo}
              dataReceived={dataReceived}
              FirstBtnClick={this.FirstBtnClick}
            />

            <MainTwo
              stageTwo={stageTwo}
              stageThree={stageThree}
              dataReceived={dataReceived}
            />

            <MainThree
              stageThree={stageThree}
              stageFour={stageFour}
              dataReceived={dataReceived}
              SecondBtnClick={this.SecondBtnClick}
            />

            <MainFour
              stageFour={stageFour}
              dataReceived={dataReceived}
              isFetched={isFetched}
              currentKey={currentKey}
              ChangeCurrentKey={this.ChangeCurrentKey}
            />
            <MainFive stageFive={stageFive} stageSix={stageSix} />
            <MainSix stageSeven={stageSeven} dataReceived={dataReceived} />
            <Footer />
          </div>
        </div>
      );
  }
}

export default MainWindow;
